/**
 * Please consider the specification of CSS. These stylus files are imported
 * on the basis of ITCSS so specifiency order is incresing.
 * Please see each section description.
 *
 * 1. settings ... Global variables, config switches
 * 2. tools ...... Default mixins, functions and tools
 * 3. generic .... Ground-zero style ( Normalize.css )
 * 4. base ....... Unclassed HTML element
 * 5. elements ... Objects and Components
 * 6. trumps ..... Overrides
 */
/**
 * Settings
 */
/**
 * Tools
 */
@mixin cf {
  :after {
    content: "";
    display: table;
    clear: both;
  }
}
/**
 * Generic
 */
/* sanitize.css | CC0 Public Domain | github.com/jonathantneal/sanitize.css */
pre,
textarea {
  overflow: auto;
}
[hidden],
audio:not([controls]),
template {
  display: none;
}
details,
main,
summary {
  display: block;
}
input[type=number] {
  width: auto;
}
input[type=search] {
  -webkit-appearance: textfield;
}
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}
progress {
  display: inline-block;
}
small {
  font-size: 75%;
}
textarea {
  resize: vertical;
}
[unselectable] {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
*,
::after,
::before {
  box-sizing: inherit;
  border-style: solid;
  border-width: 0;
}
* {
  font-size: inherit;
  line-height: inherit;
  margin: 0;
  padding: 0;
}
::after,
::before {
  text-decoration: inherit;
  vertical-align: inherit;
}
:root {
  -ms-overflow-style: -ms-autohiding-scrollbar;
  overflow-y: scroll;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-size-adjust: 100%;
  box-sizing: border-box;
  cursor: default;
  font: 16px/1.5 sans-serif;
  text-rendering: optimizeLegibility;
}
a {
  text-decoration: none;
}
audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}
button,
input,
select,
textarea {
  background-color: transparent;
  color: inherit;
  font-family: inherit;
  font-style: inherit;
  font-weight: inherit;
  min-height: 1.5em;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
}
nav ol,
nav ul {
  list-style: none;
}
select {
  -moz-appearance: none;
  -webkit-appearance: none;
}
select::-ms-expand {
  display: none;
}
select::-ms-value {
  color: currentColor;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
::-moz-selection {
  background-color: #b3d4fc;
  text-shadow: none;
}
::selection {
  background-color: #b3d4fc;
  text-shadow: none;
}
@media screen {
  [hidden~=screen] {
    display: inherit;
  }
  [hidden~=screen]:not(:active):not(:focus):not(:target) {
    clip: rect(0 0 0 0) !important;
    position: absolute !important;
  }
}
/**
 * Base
 */
html {
  min-height: 100%;
  size-adjust: 100%;
  osx-font-smoothing: grayscale;
  font-smoothing: antialiased;
  font-feature-settings: "liga" on;
}
html,
body {
  height: 100%;
}
@import url("http://fonts.googleapis.com/earlyaccess/notosansjapanese.css");
@import url("http://fonts.googleapis.com/css?family=Lato:300,400,700");
[lang="ja"] body {
  font-family: 'Noto Sans Japanese', 'Lato', sans-serif, 'ヒラギノ角ゴ Pro W3', 'Hiragino Kaku Gothic Pro', 'HiraKakuPro-W3';
}
[lang="en"] body {
  font-family: 'Lato', 'Noto Sans Japanese', sans-serif, 'ヒラギノ角ゴ Pro W3', 'Hiragino Kaku Gothic Pro', 'HiraKakuPro-W3';
}
a {
  transiton: color 0.2s linear;
  text-decoration: none;
  color: #0086b3;
}
a:hover {
  color: #006080;
}
p a {
  background-image: linear-gradient(to bottom, rgba(0,0,0,0) 50%, #00b8f5 50%);
  background-position: 0 93%;
  background-repeat: repeat-x;
  background-size: 100% 0.15rem;
  text-shadow: 0.1rem 0 #fff, 0.15rem 0 #fff, -0.1rem 0 #fff, -0.15rem 0 #fff;
}
p a:hover,
p a:avtive,
p a:focus {
  background-image: linear-gradient(to bottom, rgba(0,0,0,0) 50%, #09c 50%);
}
/**
 * Elements
 */
[am-button] {
  display: inline-block;
  font-family: inherit;
  position: relative;
  cursor: pointer;
  text-decoration: none;
  white-space: nowrap;
  vertical-align: middle;
  margin: 0;
  padding: 9px 15px;
  border-radius: 3px;
  user-select: none;
  appearance: none;
  border: none;
  box-shadow: none;
  font-size: 11px;
  letter-spacing: 0.1em;
}
[am-button],
[am-button]:hover {
  text-decoration: none;
}
[am-button]:active,
[am-button]:focus {
  outline: none;
}
[am-button~="solid"],
[am-button~="default:solid"],
[am-button~="success:solid"],
[am-button~="warning:solid"],
[am-button~="danger:solid"] {
  background: #fff;
  border: 1px solid #71a6f6;
  color: #71a6f6;
  transition: background, color, 0.2s linear;
}
[am-button~="solid"]:hover,
[am-button~="default:solid"]:hover,
[am-button~="success:solid"]:hover,
[am-button~="warning:solid"]:hover,
[am-button~="danger:solid"]:hover {
  background: #71a6f6;
  color: #fff;
}
[am-button~="fill"],
[am-button~="default:fill"],
[am-button~="success:fill"],
[am-button~="warning:fill"],
[am-button~="danger:fill"] {
  background: #71a6f6;
  border: 1px solid #71a6f6;
  color: #fff;
  transition: opacity 0.2s linear;
}
[am-button~="fill"]:hover,
[am-button~="default:fill"]:hover,
[am-button~="success:fill"]:hover,
[am-button~="warning:fill"]:hover,
[am-button~="danger:fill"]:hover {
  opacity: 0.7;
}
[am-button~="default:solid"] {
  color: #dde0e6;
  border: 1px solid #a9b1c0;
}
[am-button~="default:solid"]:hover {
  background: #dde0e6;
}
[am-button~="success:solid"] {
  color: #5cb85c;
  border: 1px solid #439a43;
}
[am-button~="success:solid"]:hover {
  background: #5cb85c;
}
[am-button~="warning:solid"] {
  color: #ec971f;
  border: 1px solid #c57a11;
}
[am-button~="warning:solid"]:hover {
  background: #ec971f;
}
[am-button~="danger:solid"] {
  color: #e94d32;
  border: 1px solid #cc3116;
}
[am-button~="danger:solid"]:hover {
  background: #e94d32;
}
[am-button~="default:fill"] {
  background: #dde0e6;
  border: 1px solid #a9b1c0;
}
[am-button~="success:fill"] {
  background: #5cb85c;
  border: 1px solid #439a43;
}
[am-button~="warning:fill"] {
  background: #ec971f;
  border: 1px solid #c57a11;
}
[am-button~="danger:fill"] {
  background: #e94d32;
  border: 1px solid #cc3116;
}
[am-button~="soft"] {
  border-radius: 200px;
}
[am-button~="hard"] {
  border-radius: 0;
}
[am-button~="large"] {
  padding: 10px 16px;
}
[am-button~="small"] {
  padding: 5px 10px;
}
[am-button-group] {
  font-size: 0;
  display: inline-block;
}
[am-button-group] [am-button] {
  border-radius: 0;
}
[am-button-group] [am-button]:first-child {
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}
[am-button-group] [am-button]:last-child {
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}
[am-button-group] [am-button~="default:fill"]:not(:last-child),
[am-button-group] [am-button~="success:fill"]:not(:last-child),
[am-button-group] [am-button~="warning:fill"]:not(:last-child),
[am-button-group] [am-button~="danger:fill"]:not(:last-child),
[am-button-group] [am-button~="default:solid"]:not(:last-child),
[am-button-group] [am-button~="success:solid"]:not(:last-child),
[am-button-group] [am-button~="warning:solid"]:not(:last-child),
[am-button-group] [am-button~="danger:solid"]:not(:last-child) {
  border-right: 0;
}
[am-table] {
  width: 100%;
}
[am-table] th,
[am-table] td {
  padding: 12px;
}
[am-table] thead tr {
  text-align: left;
  border-bottom: 2px solid #71a6f6;
}
[am-table] tbody tr {
  border-bottom: 1px solid #ccc;
  cursor: pointer;
}
[am-table~="striped"] tbody tr:nth-child(even) {
  background: #f1f4f8;
}
[am-tabs] {
  border-bottom: 1px solid #ddd;
  font-size: 0;
}
[am-tab] {
  font-size: 16px;
  margin-bottom: -1px;
  display: inline-block;
  vertical-aling: baseline;
  border-right: 1px solid #ddd;
  padding: 10px 15px;
  color: #83868b;
  cursor: pointer;
  transition: all 0.1s cubic-bezier(0.645, 0.045, 0.355, 1);
}
[am-tab]:hover {
  box-shadow: inset 0 4px #eca934;
  color: #3b372f;
  background: #eee;
}
[am-tab~="active"] {
  box-shadow: inset 0 4px #71a6f6;
  border-color: #8d9094;
  background: #fff;
  color: #3b372f;
}
[am-dashboard-wrapper] {
  height: 100%;
}
[am-dashboard-aside],
[am-dashboard-main] {
  overflow: auto;
  height: 100%;
  max-height: 100%;
}
[am-dashboard-aside] {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 210px;
}
[am-dashboard-main] {
  position: relative;
  float: right;
  top: 0;
  width: calc(100% - 210px);
}
[am-code] {
  overflow-x: hidden;
  overflow-y: auto;
  padding: 16px;
  display: block;
  font-size: 12px;
  color: #9c9;
  background: #2d2d2d;
  border-radius: 3px;
}
[am-pagination] {
  list-style: none;
  margin-left: 0;
}
[am-pagination] > li,
[am-pagination] > li > a {
  display: inline-block;
  *display: inline;
  zoom: 1;
}
[am-pagination] > li {
  margin: 0 0.2em;
}
[am-pagination] > li > a {
  vertical-align: middle;
  padding: 6px 14px;
  border-radius: 0.25em;
  border: 1px solid #e6e6e6;
  color: #000;
  transition: background, color, border-color, 0.2s linear;
}
[am-pagination] > li > a:hover {
  background: #f2f2f2;
  color: #fff;
  border-color: #ddd;
}
[am-pagination] li.active a {
  background: #71a6f6;
  color: #fff;
  border-color: #ddd;
}
[am-popup] {
  position: fixed;
}
[am-form-control] {
  transition: border 0.3s ease-in-out;
  border: 1px solid #c0cbd4;
  outline: none;
  background: #fff;
  border-radius: 0.25em;
  padding: 6px 12px;
  height: 34px;
  width: 100%;
  color: #555;
}
[am-form-control]:focus {
  border: 1px solid #0086b3;
}
[am-form-control] input-placeholder {
  color: #c4c4c4;
}
[am-form-group] {
  padding: 10px 0;
}
[am-form-group] label {
  display: inline-block;
  width: 140px;
  text-align: right;
  margin-right: 20px;
  position: relative;
  top: 2px;
}
[am-form-group] input {
  border: 1px solid #c0cbd4;
  border-radius: 0.25em;
  width: calc(100% - 164px);
}
[am-menu-hamburger] {
  cursor: pointer;
  position: relative;
}
[am-menu-hamburger] span {
  position: absolute;
  background: #000;
  width: 18px;
  height: 2px;
}
[am-menu-hamburger] span:before,
[am-menu-hamburger] span:after {
  background-color: inherit;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
}
[am-menu-hamburger] span:before {
  bottom: 5px;
}
[am-menu-hamburger] span:after {
  top: 5px;
}
[am-menu-hamburger="active"] span {
  background-color: rgba(255,255,255,0);
}
[am-menu-hamburger="active"] span:before,
[am-menu-hamburger="active"] span:after {
  top: 0;
  background-color: #5f8ee4;
}
[am-menu-hamburger="active"] span:before {
  transform: rotate(45deg);
}
[am-menu-hamburger="active"] span:after {
  transform: rotate(-45deg);
}
[am-switch] [am-toggle] {
  display: none;
}
[am-switch] [am-toggle],
[am-switch] [am-toggle]:after,
[am-switch] [am-toggle]:before,
[am-switch] [am-toggle] *,
[am-switch] [am-toggle] *:after,
[am-switch] [am-toggle] *:before,
[am-switch] [am-toggle] + [am-toggle-btn] {
  box-sizing: border-box;
}
[am-switch] [am-toggle]::selection,
[am-switch] [am-toggle]:after::selection,
[am-switch] [am-toggle]:before::selection,
[am-switch] [am-toggle] *::selection,
[am-switch] [am-toggle] *:after::selection,
[am-switch] [am-toggle] *:before::selection,
[am-switch] [am-toggle] + [am-toggle-btn]::selection {
  background: none;
}
[am-switch] [am-toggle] + [am-toggle-btn] {
  outline: 0;
  display: block;
  width: 4em;
  height: 2em;
  position: relative;
  cursor: pointer;
  user-select: none;
  background: #fbfbfb;
  padding: 2px;
  transition: all 0.4s ease;
  border: 1px solid #e8eae9;
}
[am-switch] [am-toggle] + [am-toggle-btn]:after,
[am-switch] [am-toggle] + [am-toggle-btn]:before {
  position: relative;
  display: block;
  content: "";
  width: 50%;
  height: 100%;
}
[am-switch] [am-toggle] + [am-toggle-btn]:after {
  left: 0;
}
[am-switch] [am-toggle] + [am-toggle-btn]:before {
  display: none;
}
[am-switch] [am-toggle]:checked + [am-toggle-btn]:after {
  left: 50%;
}
[am-switch] [am-toggle] + [am-toggle-btn]:after {
  background: #fbfbfb;
  transition: left 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275), padding 0.3s ease, margin 0.3s ease;
  box-shadow: 0 0 0 1px rgba(0,0,0,0.1), 0 4px 0 rgba(0,0,0,0.08);
}
[am-switch] [am-toggle] + [am-toggle-btn]:active {
  box-shadow: inset 0 0 0 2em #e8eae9;
}
[am-switch] [am-toggle] + [am-toggle-btn]:active:after {
  padding-right: 0.8em;
}
[am-switch] [am-toggle]:checked + [am-toggle-btn] {
  background: #86d993;
}
[am-switch] [am-toggle]:checked + [am-toggle-btn]:active {
  box-shadow: none;
}
[am-switch] [am-toggle]:checked + [am-toggle-btn]:active:after {
  margin-left: -0.8em;
}
[am-switch~="round"] [am-toggle] + [am-toggle-btn],
[am-switch~="round"] [am-toggle] + [am-toggle-btn]:after {
  border-radius: 2em;
}
[am-loader="circle"] {
  font-size: 10px;
  text-indent: -9999em;
  border-top: 2px solid #f1f4f8;
  border-right: 2px solid #f1f4f8;
  border-bottom: 2px solid #f1f4f8;
  border-left: 2px solid #71a6f6;
  transform: translateZ(0);
}
[am-loader],
[am-loader]:after {
  border-radius: 50%;
  width: 50px;
  height: 50px;
}
[am-loader]:hover {
  animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-moz-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-o-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
[am-label] {
  border: 1px solid #ccc;
  background: #fff;
  border-radius: 3px;
  width: 24px;
  line-height: 24px;
  display: inline-block;
  height: 24px;
  cursor: pointer;
  text-align: center;
  margin-left: 7px;
  position: relative;
  top: 1px;
}
[am-label]:hover {
  background: #efefef;
}
/**
 * Trumps
 */
@media (max-width: 991px) {
  [am-dashboard-main] {
    width: 100%;
  }
}
